.CreateTodoButton {
  position: relative;
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgb(97 218 250 / 50%);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 58px;
  width: 58px;
  margin: auto;

  transform: rotate(0);
  transition: .3s ease;
  z-index: 1;
}

.CreateTodoButton:hover {
  transform: rotate(224deg);
}