.TodoItem {
  background-color: rgb(255, 255, 255);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  border-radius: 8px;
  color: #757575;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}

.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}

.Icon-check {
  left: 12px;
}

.Icon-check:hover {
  color: #4caf50;
}

.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  top: -24px;
  right: 0;
}

.Icon-delete:hover {
  color: red;
}